* {
  box-sizing: border-box;
}

body {
  background-color: #f4f6ee;
  margin: 0;
  padding: 0;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

ul, ol {
  margin: 0;
  padding-left: 0;
}

img {
  height: auto;
  display: block;
}

.loader {
  width: 12px;
  height: 12px;
  color: #2d9596;
  box-sizing: border-box;
  border-radius: 50%;
  margin: 15px auto;
  animation: animloader 1s linear infinite alternate;
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@keyframes animloader {
  0% {
    box-shadow: -38px -6px, -14px 6px, 14px -6px;
  }

  33% {
    box-shadow: -38px 6px, -14px -6px, 14px 6px;
  }

  66% {
    box-shadow: -38px -6px, -14px 6px, 14px -6px;
  }

  100% {
    box-shadow: -38px 6px, -14px -6px, 14px 6px;
  }
}

.list {
  margin-bottom: 10px;
  list-style: none;
}

.breed-select {
  max-width: 300px;
  margin: 20px 15px 5px;
}

.cat-info {
  flex-direction: row;
  gap: 26px;
  margin: 20px 16px;
  display: flex;
}

.content-wrapper {
  max-width: 50%;
}

.image {
  max-width: 40%;
  object-fit: cover;
}

.main-title {
  margin-bottom: 16px;
  font-size: 34px;
}

.description {
  letter-spacing: .02em;
  margin-bottom: 10px;
  font-size: 18px;
}

.list-item:not(:last-child) {
  margin-bottom: 4px;
}

.link {
  color: #2d9596;
  text-decoration: none;
  transition: color .2s;
  display: block;
}

.link:hover {
  color: #67e1c1;
}

.is-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

